.reviews-sorting {
	padding: 16px 0;
	border-bottom: 1px solid rgba($dark, 0.1);
	@include clr;
	@include md {
		padding: 16px 0; }
	@include sm {
		padding: 8px 0 16px; }
	&__amount {
		float: left;
		@include md {
			padding: 4px 0; }
		@include sm {
			padding: 28px 0 4px;
			width: 128px; } }
	&__label {
		float: left;
		padding: 8px 0;
		width: 100px;
		color: $text;
		@include sreg;
		@include f(12, 16);
		@include sm {
			float: none;
			padding: 0 0 8px;
			width: 100%; } }
	&__filter {
		float: right;
		width: 348px;
		@include clr;
		@include sm {
			float: none;
			padding-left: 128px;
			width: auto; }
		.select {
			float: right;
			width: 248px;
			@include sm {
				width: 100%; } } } }
