.score {
	position: relative;
	padding-top: 24px;
	@include clr;
	@include md {
		padding-top: 0; }
	&__row {
		margin: 0 -8px;
		@include clr; }
	&__sidebar {
		float: left;
		width: 25%;
		padding: 0 8px;
		@include md {
			width: 100%; }
		&:after {
			content: '';
			display: none;
			margin-top: -1px;
			border-bottom: 1px solid rgba(29, 46, 73, 0.1);
			@include sm {
				display: block; } } }
	&__content {
		float: right;
		padding: 0 8px;
		width: 75%;
		@include md {
			padding: 22px 8px 0;
			width: 100%; }
		@include sm {
			padding-top: 16px; } }
	&__box {
		margin-bottom: 24px;
		border: 1px solid rgba(28, 46, 73, 0.1);
		border-radius: 2px;
		// min-height: 470px
		background: $white;
		// +md
 }		/// min-height: 430px
	&__popular {
		margin-bottom: -32px;
		@include md {
			margin-bottom: -24px; } } }

.score-tabs {
	&:after {
		content: "";
		display: block;
		margin-top: -1px;
		border-bottom: 1px solid rgba($dark, 0.1); }
	li {
		&:not(:last-child) {
			padding-right: 40px;
			@include sm {
				// padding-right: 16px
				padding-right: 24px; } }
		a {
			@include sm {
				@include f(14, 24, 0); } } } }
