.page {
	padding-bottom: 40px;
	@include md {
		padding-bottom: 24px; }
	&__heading {
		position: relative;
		padding-bottom: 16px;
		border-bottom: 1px solid rgba($dark, 0.1);
		&_top {
			padding-top: 16px;
			@include sm {
				padding-top: 8px; } }
		&_border-none-md {
			@include md {
				padding-bottom: 8px;
				border-bottom: none; } } }
	&__row {
		margin: 0 -8px;
		@include clr;
		@include md {
			overflow: hidden; }
		@include sm {
			overflow: visible; } }
	&__sidebar {
		float: left;
		padding: 20px 8px 0;
		width: 25%;
		@include filterOpened {
			@include md {
				opacity: 1;
				transform: translate3d(0,0,0); } }
		@include md {
			padding: 12px 8px 0;
			width: 33.33%;
			opacity: 0;
			transform: translate3d(-100%,0,0);
			transition: opacity 0.35s ease, transform 0.35s ease; }
		@include sm {
			position: fixed;
			z-index: 1000;
			top: 0;
			left: 0;
			bottom: 0;
			overflow: auto;
			padding: 12px 0;
			width: 248px;
			opacity: 1;
			background: whitesmoke; }
		&_non-top {
			padding-top: 0;
			margin-top: -16px;
			@include md {
				margin-top: 0; }
			@include md {
				padding-top: 0; }
			@include sm {
				padding-top: 0; } } }
	&__content {
		float: right;
		padding: 24px 8px 0;
		width: 75%;
		@include filterOpened {
			@include md {
				transform: translate3d(33.33%,0,0); }
			@include sm {
				@include gpu; } }
		@include md {
			padding-top: 16px;
			margin-left: -33.33%;
			width: 100%;
			@include gpu;
			transition: transform 0.35s ease; }
		@include sm {
			margin-left: 0; }
		&_non-top {
			padding-top: 0;
			@include md {
				margin-top: 0; }
			@include md {
				padding-top: 0; }
			@include sm {
				padding-top: 0; } } }
	&__sorting {
		float: right;
		padding: 16px 8px 0;
		margin-bottom: -8px;
		width: 75%;
		@include md {
			margin-bottom: 0;
			width: 100%; } }
	&__partners {
		padding-bottom: 8px; }
	&__breadcrumbs {
		padding-bottom: 8px; }
	&__pagination {
		margin-bottom: 24px;
		@include sm {
			margin-bottom: 40px; } }
	&__item {
		border-bottom: 1px solid rgba($dark, 0.1); } }

.page-title {
	&__list {
		margin-bottom: -16px;
		font-size: 0;
		white-space: nowrap;
		li {
			display: inline-block;
			vertical-align: top;
			padding-right: 40px;
			@include sm {
				padding-right: 24px; } } }
	&__link {
		position: relative;
		display: block;
		padding-bottom: 16px;
		color: $dark;
		@include reg;
		@include f(32, 40, 1);
		@include t(color);
		@include md {
			@include f(24, 32, 0); }
		@include sm {
			@include f(18, 32); }
		@include hover {
			color: $blue; }
		&.is-active {
			color: $blue;
			&:before {
				transform: translate3d(0,0,0) scaleX(1); } }
		.ps-active-x & {
			&:before {
				display: none; } }
		&:before {
			content: "";
			position: absolute;
			bottom: 0;
			left: 0;
			right: 0;
			border-bottom: 2px solid $blue;
			transform: translate3d(0,0,0) scaleX(0);
			@include t(transform); } } }

.page-intro {
	padding-top: 8px;
	@include md {
		padding-top: 8px; }
	h1,
	.title-h1 {
		margin-bottom: 8px;
		@include md {
			margin-bottom: 0; }
		@include sm {
			margin-bottom: 8px;
			@include f(24, 32, 1); } }
	&__header {
		padding-bottom: 16px;
		@include clr;
		// +md
		/// margin-top: -8px
		// +sm
 }		// 	margin: 0
	&__meta {
		float: left;
		padding: 8px 0;
		color: $text;
		@include sreg;
		@include f(13, 16);
		@include md {
			@include f(12, 16); }
		@include sm {
			padding: 0;
			width: 41%; }
		a {
			color: $blue;
			@include t(color);
			@include hover {
				color: $blue-light; }
			@include active {
				color: $blue-dark; } }
		span {
			@include sm {
				display: block; } } }
	&__action {
		float: right;
		font-size: 0;
		@include sm {
			width: 59%;
			text-align: right; }
		li {
			display: inline-block;
			vertical-align: top;
			&:not(:first-child) {
				padding-left: 16px; } } }
	&__info {
		float: right;
		padding: 8px 0;
		font-size: 0;
		@include sm {
			padding: 0;
			width: 59%;
			text-align: right; }
		li {
			display: inline-block;
			vertical-align: top;
			color: $text;
			@include sreg;
			@include f(13, 16);
			@include sm {
				display: block;
				@include f(12, 16); }
			&:not(:first-child) {
				padding-left: 12px; } }
		span,
		strong {
			display: inline-block;
			vertical-align: top; }
		strong {
			@include ss; }
		&-blue {
			color: $blue; }
		&-red {
			color: $red; } } }

.page-shares {
	border-bottom: 1px solid rgba($dark, 0.1);
	&_bottom {
		margin-bottom: 24px; } }

.grid {
	&__row {
		margin: 0 -8px;
		@include clr; }
	&__col {
		float: left;
		width: 100%;
		padding: 0 8px;
		min-height: 1px;
		&_xs {
			width: 25%; }
		&_sm {
			width: 33.33%; }
		&_md {
			width: 50%; }
		&_lg {
			width: 75%; } } }

.l-content {
	&__container {
		margin: 0 -8px;
		@include clr;
		@include md {
			margin: 0 -6px; }
		&_review,
		&_news,
		&_product-slider {
			padding-top: 16px;
			@include sm {
				margin: 0 -16px; } }
		&_product {
			@include md {
				padding-bottom: 8px; }
			@include sm {
				padding-bottom: 16px; } } }
	&__col {
		float: left;
		padding: 0 8px 16px;
		@include md {
			padding: 0 6px 16px; }
		&_sm {
			width: 33.33%; }
		&_lg {
			width: 66.66%; }
		&_review {
			@include sm {
				width: 244px; } }
		&_news {
			@include sm {
				width: 242px; } }
		&_product {
			@include sm {
				width: 50%; } }
		&_product-slide {
			@include sm {
				width: 264px; } } } }
