.category {
	margin-bottom: 110px;
	color: $text;
	&__box {
		position: relative;
		padding-bottom: 8px;
		margin-bottom: 12px;
		border-bottom: 1px solid rgba($dark, 0.1);
		@include sm {
			padding: 0 8px 8px; } }
	&__title {
		position: relative;

		display: block;
		padding: 4px 32px 4px 8px;
		margin: 0 -8px 4px;
		border-radius: 2px;

		color: $dark;

		@include reg;
		@include f(15, 24, 0.27);
		transition: color 0.25s ease, background 0.25s ease;
		@include md {
			padding: 4px 30px 4px 6px;
			margin: 0 -6px 4px; }
		@include sm {
			padding: 4px 32px 4px 8px;
			margin: 0 0 4px; }
		&.is-active {
			color: $blue;
			background: rgba($dark, 0.07);
			.icon-down {
				transform: translate3d(0,0,0) rotate(180deg); } }
		@include hover {
			color: $blue;
			background: rgba($dark, 0.07); }
		&_parts {
			padding: 0;
			transition: background 0.25s ease; }
		&-in {
			display: block;
			overflow: hidden;
			padding: 4px 8px 4px 8px;
			color: inherit;
			transition: background 0.25s ease;
			@include hover {
				background: rgba($dark, 0.07); } } }
	.icon-down {
		position: absolute;
		top: 50%;
		right: 15px;
		margin-top: -3px;
		color: $dark;
		font-size: 6px;
		@include gpu;
		@include t(transform);
		@include md {
			right: 13px; }
		@include sm {
			right: 7px; } }
	&__arrow {
		float: right;
		border-radius: 0 2px 2px 0;
		width: 40px;
		height: 100%;
		cursor: pointer;
		transition: background 0.25s ease;
		@include hover {
			background: rgba($black, 0.07); }
		&_title {
			height: 32px; }
		&_default {
			height: 24px; } }
	&__link {
		position: relative;
		padding-right: 24px;
		&_parts {
			padding: 0;
			margin: 0 -8px;
			transition: background 0.25s ease;
			&.is-active {
				color: $blue;
				background: rgba($dark, 0.1); }
			@include hover {
				color: $blue;
				background: rgba($dark, 0.1); } }
		&-in {
			display: block;
			overflow: hidden;
			margin: 0 !important;
			color: inherit !important; }
		&.is-active {
			.icon-down {
				transform: translate3d(0,0,0) rotate(180deg); } } }
	&__block {
		position: relative;
		display: none;
		padding-bottom: 8px;
		&.is-open {
			display: block; }
		a {
			display: block;
			padding: 0 8px;
			margin: 0 -8px;
			border-radius: 2px;

			color: $text;

			@include sreg;
			@include f(13, 24);
			transition: color 0.2s ease, background 0.2s ease;
			@include md {
				padding: 0 6px;
				margin: 0 -6px; }
			@include sm {
				padding: 0 8px;
				margin: 0; }
			&.is-active {
				color: $blue;
				background: rgba($dark, 0.07); }
			@include hover {
				color: $blue;
				background: rgba($dark, 0.07); } } }
	&__item {
		&:last-child {
			.category {
				&__dropdown {
					border-bottom: none;
					margin-bottom: 0; } } } }
	&__dropdown {
		display: none;
		padding: 8px 0;
		margin: 8px 0 8px 16px;
		border-top: 1px solid rgba($dark, 0.1);
		border-bottom: 1px solid rgba($dark, 0.1);
		&.is-open {
			display: block; }
		a {
			@include hover {
				background: rgba($dark, 0.17); } } } }
