.product-review {
	&_score {
		.product-review {
			&__title {
				padding-bottom: 16px;
				margin-bottom: 24px;
				border-bottom: 1px solid rgba($dark, 0.1);
				@include md {
					display: block;
					margin-bottom: 16px; }
				@include sm {
					padding-bottom: 10px; } }
			&__steps {
				width: 59%;
				@include md {
					width: 64%; }
				@include sm {
					width: 100%; } }
			&__info {
				width: 41%;
				@include md {
					width: 36%; }
				@include sm {
					width: 100%; } } } }
	&__title {
		margin-bottom: 16px;
		@include md {
			display: none; } }
	&__rating {
		margin-bottom: 8px; }
	&__body {
		@include clr; }
	&__steps {
		float: left;
		width: 54%;
		@include md {
			width: 64%; }
		@include sm {
			width: 100%;
			padding-bottom: 16px; }
		&-in {
			max-width: 344px;
			@include sm {
				max-width: 100%; } } }
	&__info {
		float: right;
		width: 46%;
		@include md {
			width: 36%; }
		@include sm {
			width: 100%; }
		.btn {
			@include sm {
				width: 100%; } } }
	&__label {
		margin-bottom: 8px;
		color: $text;
		@include sreg;
		@include f(13, 16); }
	&__value {
		margin-bottom: 16px;
		max-width: 270px;
		min-height: 48px;
		color: $dark;
		@include reg;
		@include f(15, 24, 1);
		@include sm {
			min-height: 1px; } } }
