@include font("SegoeUI Regular", SegoeUI);
@include font("SegoeUI Semibold", SegoeUI-Semibold);

@mixin sreg {
	font-family: "SegoeUI Regular", Arial, sans-serif; }

@mixin ss {
	font-family: "SegoeUI Semibold", Arial, sans-serif; }


/////////////////////////PFAgoraSlabPro/////////////////////
@include font("PFAgoraSlabPro Regular", PFAgoraSlabPro-Regular);
@include font("PFAgoraSlabPro Italic", PFAgoraSlabPro-Italic);
@include font("PFAgoraSlabPro Medium", PFAgoraSlabPro-Medium);
@include font("PFAgoraSlabPro Bold", PFAgoraSlabPro-Bold);

@mixin reg {
	font-family: "PFAgoraSlabPro Regular", Arial, sans-serif; }

@mixin italic {
	font-family: "PFAgoraSlabPro Italic", Arial, sans-serif; }

@mixin med {
	font-family: "PFAgoraSlabPro Medium", Arial, sans-serif; }

@mixin bold {
	font-family: "PFAgoraSlabPro Bold", Arial, sans-serif; }
