.brand-item {
	padding: 24px 0;
	@include clr;
	@include md {
		padding: 16px 0; }
	@include sm {
		padding-top: 20px; }
	&__img {
		float: left;
		padding: 0 8px;
		width: 25%;
		@include md {
			width: 33.33%; }
		@include sm {
			width: 100%;
			margin-bottom: 16px; } }
	&__box {
		position: relative;
		display: block;
		padding: 16px;
		border: 1px solid rgba(28, 46, 73, 0.1);
		border-radius: 2px;
		height: 206px;
		background: $white;
		@include vertical;
		@include md {
			height: 193px; }
		@include sm {
			margin: 0 auto;
			max-width: 232px; }
		img {
			display: inline-block;
			vertical-align: middle;
			max-width: 100%;
			max-height: 100%; } }
	.sm-rating {
		position: absolute;
		bottom: 17px;
		right: 16px; }
	&__content {
		float: left;
		padding: 0 8px 0 24px;
		width: 75%;
		@include md {
			padding-left: 20px;
			width: 66.66%; }
		@include sm {
			padding-left: 0;
			width: 100%; } }
	&__text {
		overflow: hidden;
		margin-bottom: 6px;
		height: 168px;
		@include md {
			margin-bottom: 16px;
			height: 144px; }
		@include sm {
			height: auto;
			max-height: 240px; }
		p {
			margin-bottom: 24px;
			@include md {
				@include f(13, 24);
				margin-bottom: 0; } } }
	&__more {
		text-align: right; } }
