@import "card/card-item";
@import "card/card-gallery";
@import "card/card-info";
@import "card/card-main";

.card-sliders {
	padding-bottom: 8px;
	@include md {
		padding-bottom: 0; } }

.card {
	&__container {
		padding: 31px 15px 15px;
		margin: 16px 0 40px;
		border: 1px solid rgba(28, 46, 73, 0.1);
		border-radius: 2px;
		background: #ffffff;
		@include clr;
		@include md {
			padding: 23px 15px 15px;
			margin-bottom: 24px; } }
	&__col {
		padding: 0 16px;
		@include md {
			padding: 0; }
		&_item,
		&_info {
			float: left;
			width: 52.38%; }
		&_gallery {
			float: right;
			width: 47.62%; }
		&_item,
		&_info,
		&_gallery {
			@include md {
				float: none;
				width: 100%; } }
		&_item {
			@include md {
				padding-right: 72px; }
			@include sm {
				padding: 0; } }
		&_gallery {
			@include md {
				padding: 0 72px; }
			@include sm {
				padding: 0; } } } }
