.partners {
	&__row {
		padding: 8px 0;
		margin: 0 -8px;
		@include clr;
		@include md {
			margin: 0 -13px; }
		@include sm {
			margin: 0 -12px; } }
	&__col {
		float: left;
		padding: 8px;
		width: (100%/6);
		@include md {
			padding: 8px 13px;
			width: 25%; }
		@include sm {
			padding: 8px 20px;
			width: 50%; } } }

.partner {
	position: relative;
	display: block;
	margin: 0 auto;
	border: 1px solid rgba(28, 46, 73, 0.1);
	border-radius: 2px;
	height: 128px;
	padding-bottom: {}

	background: #fff;
	@include sm {
		height: 96px; }
	@include hover {
		&:before {
			opacity: 1; }
		img {
			opacity: 1;
			@include gray-zero; } }
	&:before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		opacity: 0;
		box-shadow: 0 8px 16px rgba(7, 20, 39, 0.1);
		@include t(opacity); }
	&_sm {
		height: 96px;
		@include md {
			height: 128px; }
		@include sm {
			height: 96px; } }
	&__inner {
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		padding: 10px;
		@include vertical; }
	img {
		display: inline-block;
		vertical-align: middle;
		max-width: 90%;
		max-height: 90%;
		opacity: 0.3;
		@include gray;
		transition: filter 0.25s ease, opacity 0.25s ease; } }
