.overview-item {
	padding-right: 16px;
	@include clr;
	@include md {
		padding-right: 0; }
	@include hover {
		&:before {
			opacity: 1; } }
	&:before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		border-radius: 2px;
		opacity: 0;
		background: $white;
		box-shadow: 0 8px 16px rgba(7, 20, 39, 0.1);
		@include t(opacity); }
	&__img {
		position: relative;
		z-index: 2;
		float: left;
		border-radius: 2px;
		width: 67%;
		height: 296px;
		font-size: 0;
		@include md {
			height: 272px; }
		@include sm {
			width: 100%;
			height: auto; }
		img {
			border-radius: 2px;
			width: 100%;
			height: 100%;
			object-fit: cover; } }
	&__content {
		position: relative;
		z-index: 2;
		float: right;
		padding: 16px 0 0 32px;
		width: 33%;
		@include md {
			padding-left: 16px; }
		@include sm {
			width: 100%;
			padding: 16px 28px 0; } }
	&__sticker {
		margin-bottom: 8px; }
	&__title {
		overflow: hidden;
		margin-bottom: 8px;
		max-height: 48px;
		color: $dark;
		@include reg;
		@include f(15, 24, 1);
		a {
			display: block;
			color: inherit;
			@include t(color);
			@include hover {
				color: $blue; } } }
	&__description {
		overflow: hidden;
		margin-bottom: 32px;
		height: 48px;
		color: $text;
		@include sreg;
		@include f(13, 16);
		@include md {
			margin-bottom: 24px; } }
	&__actions {
		padding-bottom: 15px;
		margin-bottom: 16px;
		border-bottom: 1px solid rgba($dark, 0.1);
		font-size: 0;
		li {
			display: inline-block;
			vertical-align: top;
			padding-right: 16px; } }
	&__more {
		text-align: center;
		.btn {
			@include md {
				width: 100%; } } } }
