.looking-for {
	&__row {
		padding: 24px 0;
		margin: 0 -16px;
		@include clr;
		@include md {
			padding: 16px 0 24px;
			margin: 0 -10px; } }
	&__col {
		float: left;
		padding: 0 16px;
		width: 33.33%;
		@include md {
			padding: 0 10px; }
		@include sm {
			width: 100%; } }
	li {
		color: $dark;
		@include sreg;
		@include f(13, 16);
		padding: 8px 0 0;
		@include sm {
			padding: 0;
			@include f(13, 24); }
		a {
			color: inherit;
			@include t(color);
			@include hover {
				color: $blue; } } } }
