.article {
	&__text {
		overflow: hidden;
		padding-bottom: 8px;
		p {
			margin-bottom: 16px; } }
	&__text_indent {
		padding: 0 0 16px 32px;
		@include sm {
			padding: 0; } }
	&__row {
		margin: 0 -24px;
		@include clr;
		@include md {
			margin: 0 -16px; } }
	&__col {
		float: left;
		padding: 0 24px;
		width: 50%;
		@include md {
			padding: 0 16px; }
		@include sm {
			width: 100%; } }
	h1,
	.title-h1 {
		margin-bottom: 8px;
		@include sm {
			@include f(24, 32, 1); } }
	h2,
	.title-h2 {
		margin-bottom: 16px;
		@include md {
			@include f(15, 24, 1); } }
	.blockquote {
		margin-bottom: 24px; } }

.article-item {
	img {
		display: block;
		border: 1px solid rgba(28, 46, 73, 0.1);
		border-radius: 2px;
		width: 100%;
		height: 296px;
		object-fit: cover;
		@include md {
			height: 274px; } }
	&__img {
		margin-bottom: 24px;
		@include sm {
			margin: 0 -16px 24px; } } }

.article-figure {
	margin-bottom: 24px;
	&__info {
		font-size: 0;
		text-align: center;
		@include clr; }
	&__title,
	&__rating {
		display: inline-block;
		vertical-align: top;
		padding: 0 4px;
		@include md {
			padding: 0 7px; } }
	&__title {
		color: $text;
		@include reg;
		@include f(15, 24, 1);
		@include md {
			@include f(13, 24, 1); }
		a {
			color: $blue;
			@include t(color);
			@include hover {
				color: $blue-light; }
			@include active {
				color: $blue-dark; } } }
	img {
		display: block;
		margin: 0 auto 24px;
		width: 100%;
		max-width: 288px;
		height: 288px;
		object-fit: scale-down;
		@include md {
			height: 280px; }
		@include sm {
			height: 288px; } } }

//text

h1, h2, h3, h4, h5, h6, strong, b, em, i, th, cite {
	font-weight: normal;
	font-style: normal; }

h1,
h2,
h3,
.title-h1,
.title-h2,
.title-h3 {
	color: $dark;
	@include reg; }

h1,
.title-h1 {
	@include f(32, 40, 0.58);
	@include md {
		@include f(24, 32, 0.44); }
	@include sm {
		@include f(18, 32, 0.4); }
	span {
		color: $darkGray; } }

h2,
.title-h2 {
	@include f(26, 32, 0.47);
	@include md {
		@include f(24, 32, 0.44); }
	@include sm {
		@include f(18, 24, 0.4); } }
.title-h2 {
	&_sm {
		@include f(22, 32, 1);
		@include md {
			@include f(20, 24, 1); }
		@include sm {
			@include f(16, 24, 1); } } }

h3,
.title-h3 {
	@include f(18, 24, 0.27);
	@include md {
		@include f(16, 24, 0.27); }
	small {
		color: $text; } }
.title-h3 {
	&_sm {
		@include f(18, 32, 1);
		@include md {
			@include f(15, 24, 1); } } }


p, ul, ol {
	&:not([class]) {
		color: $text;
		@include f(14, 24);
		@include md {
			@include f(12, 24); }
		a {
			color: $blue;
			@include t(color);
			@include hover {
				color: $blue-light; }
			@include active {
				color: $blue-dark; } } } }


a {
	text-decoration: none; }

.rub {
	position: relative;
	display: inline-block;
	vertical-align: bottom;
	@include reg;
	@include f(15, 24, 1);
	&:before {
		content: "";
		position: absolute;
		bottom: 37%;
		left: 0;
		right: 0.15em;
		border-bottom: 1px solid currentColor; } }

.label {
	display: block;
	color: $text;
	@include sreg;
	@include f(12, 16);
	&_bottom {
		padding-bottom: 8px; }
	&_light {
		color: #f5f5f5; } }

.text-center {
	text-align: center; }
.text-left {
	text-align: left; }
.text-right {
	text-align: right; }

.float-none {
	float: none !important; }
