.reviews-form {
	&_score {
		.reviews-form {
			&__fieldset {
				float: none;
				padding-right: 0;
				width: 100%;
				max-width: 592px;
				@include md {
					padding-right: 96px;
					max-width: 100%; }
				@include sm {
					padding-right: 0; } }
			&__info {
				float: none;
				padding: 0 0 40px 176px;
				width: 100%;
				max-width: 592px;
				@include md {
					padding-right: 96px;
					max-width: 100%; }
				@include sm {
					padding: 12px 0 16px; } }
			&__footnote {
				float: left;
				padding-bottom: 0;
				@include sm {
					float: none;
					padding-bottom: 16px; } }
			&__authorization {
				float: right;
				padding-top: 8px;
				@include sm {
					float: none;
					padding: 0; } } } }
	&__header {
		border-bottom: 1px solid rgba($dark, 0.1); }
	&__title {
		margin-bottom: 16px;
		@include sm {
			margin-bottom: 20px; } }
	&__body {
		padding: 16px 0 0; }
	&__subtitle {
		margin-bottom: 16px;
		color: $dark;
		@include reg;
		@include f(15, 24);
		@include md {
			margin-bottom: 8px; } }
	&__section {
		@include clr; }
	&__fieldset {
		float: left;
		padding-right: 56px;
		width: 66%;
		@include md {
			padding-right: 96px;
			width: 100%; }
		@include sm {
			padding-right: 0; } }
	&__info {
		float: right;
		width: 34%;
		padding: 8px 0 40px;
		@include clr;
		@include md {
			padding: 0 96px 40px 176px;
			width: 100%; }
		@include sm {
			padding: 12px 0 16px; } }
	&__field {
		margin-bottom: 16px;
		@include clr;
		&_rating {
			.reviews-form {
				&__label {
					@include sm {
						padding: 8px 24px 8px 0; } } } }
		&_radios {
			.reviews-form {
				&__label {
					@include sm {
						padding: 0 24px 8px 0; } } } }
		&_rating,
		&_radios {
			@include sm {
				margin-bottom: 8px; }
			.reviews-form {
				&__label {
					@include sm {
						float: left;
						width: 128px; } }
				&__box {
					@include sm {
						padding-left: 128px; } } } } }
	&__label {
		float: left;
		padding: 8px 24px 8px 0;
		width: 176px;
		color: $dark;
		@include reg;
		@include f(15, 24);
		@include sm {
			float: none;
			display: block;
			padding: 0 0 8px;
			width: 100%; } }
	&__box {
		padding-left: 176px;
		@include sm {
			padding-left: 0; } }
	&__rating {
		padding: 8px 16px; }
	&__list {
		font-size: 0;
		@include sm {
			margin-right: -13px; }
		li {
			display: inline-block;
			vertical-align: top;
			padding: 8px 0;
			@include sm {
				display: block;
				padding: 0; }
			&:not(:last-child) {
				margin-right: 24px;
				@include sm {
					margin-right: 0; } } } }
	&__text {
		padding-bottom: 16px;
		@include md {
			padding-bottom: 24px; }
		@include sm {
			padding-bottom: 16px; } }
	&__footnote {
		padding-bottom: 40px;
		max-width: 220px;
		@include md {
			float: left;
			padding-bottom: 0; }
		@include sm {
			float: none;
			padding-bottom: 16px; } }
	&__authorization {
		@include md {
			float: right;
			padding-top: 8px; }
		@include sm {
			float: none;
			padding: 0; }
		.btn {
			@include sm {
				width: 100%; } } }
	&__text,
	&__footnote {
		color: $text;
		@include sreg;
		@include f(14, 24);
		@include md {
			@include f(12, 24); } }
	&__footer {
		position: relative;
		padding: 24px 0 0;
		border-top: 1px solid rgba($dark, 0.1);
		@include md {
			padding: 24px 0 0; }
		@include sm {
			padding: 16px 0 0; } }
	&__submit {
		text-align: center;
		.btn {
			@include sm {
				width: 100%; } } }
	&__agreement {
		position: absolute;
		top: 32px;
		right: 0;
		padding: 4px 0;
		width: 34%;
		font-size: 0;
		@include md {
			width: 220px; }
		@include sm {
			position: relative;
			top: auto;
			right: auto;
			padding: 20px 0 0;
			width: auto; } } }
