.row-item {
	padding: 15px;
	border: 1px solid rgba(28, 46, 73, 0.1);
	border-radius: 2px;
	background: $white;
	@include clr;
	@include sm {
		padding: 15px 0 16px;
		border: none;
		border-top: 1px solid rgba(28, 46, 73, 0.1); }
	&__figure {
		position: relative;
		float: left;
		width: 104px; }
	&__discount {
		position: absolute;
		top: -8px;
		right: -8px; }
	&__img {
		display: block;
		padding: 16px;
		height: 104px;
		@include vertical;
		img {
			display: inline-block;
			vertical-align: middle;
			max-width: 100%;
			max-height: 100%; } }
	&__right {
		float: right;
		width: 168px;
		@include sm {
			float: left;
			padding-left: 24px;
			width: 152px; } }
	&__body {
		overflow: hidden;
		padding: 0 24px 0 32px;
		@include md {
			padding: 0 24px; }
		@include sm {
			clear: both;
			overflow: visible;
			padding: 14px 0 0; } }
	&__title {
		color: $dark;
		@include reg;
		@include f(15, 16, 1); }
	&__text {
		color: $text;
		@include sreg;
		@include f(12, 16); }
	&__title,
	&__text {
		margin-bottom: 8px;
		@include text-overflow; }
	&__shop {
		float: left;
		font-size: 0;
		white-space: nowrap; }
	&__label {
		color: $text; }
	&__value {
		color: $dark; }
	&__label,
	&__value {
		display: inline-block;
		vertical-align: top;
		padding-right: 8px;
		@include sreg;
		@include f(13, 24); }
	&__rating {
		float: left;
		padding: 4px 0; }
	&__price {
		margin-bottom: 24px; }
	&__current {
		margin-bottom: 8px;
		color: $blue;
		@include bold;
		@include f(15, 16, 1); }
	&__old {
		color: $text;
		@include med;
		@include f(13, 16, 1);
		text-decoration: line-through; }
	&__btn {
		@include sm {
			padding: 4px 16px; } } }
