.breadcrumbs {
	font-size: 0;
	li {
		display: inline-block;
		vertical-align: top;

		color: $darkGray;

		@include sreg;
		@include f(12, 16);
		@include sm {
			display: none; }
		&:not(:first-child) {
			&:before {
				content: '-';
				display: inline-block;
				vertical-align: top;
				padding: 0 8px; } }
		&:first-child,
		&:nth-child(2),
		&:nth-child(3),
		&:last-child {
			@include sm {
				display: inline-block;
				vertical-align: top; } }
		&:last-child {
			@include sm {
				max-width: 100%;
				@include text-overflow; } }
		&:nth-child(3) {
			a {
				@include sm {
					display: none; } }
			&:after {
				@include sm {
					content: "...";
					display: inline-block;
					vertical-align: top; } }
			&.is-active {
				@include sm {
					display: block; }
				a {
					@include sm {
						display: inline; } }
				&:before,
				&:after {
					@include sm {
						display: none; } } } }
		&.is-active {
			color: $blue;
			@include sm {
				display: block; }
			&:before {
				@include sm {
					display: none; } } } }
	a {
		color: inherit;
		transition: color 0.15s ease-in-out;
		@include hover {
			color: $blue; }
		&.is-active {
			color: $blue; } } }
