.shares {
	position: relative;
	padding: 16px 0;
	@include clr;
	@include sm {
		text-align: center; }
	&__title,
	&__edit {
		float: left;
		@include sm {
			float: none;
			display: inline-block;
			vertical-align: top; } }
	&__title {
		padding: 8px 8px 8px 0; }
	&__btns {
		float: right;
		font-size: 0;
		@include sm {
			float: none;
			padding-bottom: 16px; }
		li {
			display: inline-block;
			vertical-align: top;
			padding-left: 24px;
			@include md {
				padding-left: 16px; }
			@include sm {
				padding: 0 16px; } } }
	&__btn {
		float: left;
		max-width: 216px;
		@include sm {
			float: none; } }
	&__inner {
		float: right;
		padding-left: 240px;
		margin-left: -216px;
		width: 100%;
		text-align: right;
		@include sm {
			float: none;
			padding-left: 0;
			margin-left: 0;
			text-align: center; }
		.shares {
			&__title,
			&__btns {
				float: none;
				display: inline-block;
				vertical-align: top;
				@include sm {
					display: block; } }
			&__title {
				padding-right: 0;
				@include sm {
					padding-top: 0; } } } } }
