.card-main {
	margin-bottom: 40px;
	border: 1px solid rgba(28, 46, 73, 0.1);
	border-radius: 2px;
	background: $white;
	&__tabs {
		position: relative;
		&:after {
			content: "";
			display: block;
			margin: -1px 32px  0;
			border-bottom: 1px solid rgba($dark, 0.1);
			@include md {
				margin: -1px 16px 0; } }
		li {
			&:not(:last-child) {
				padding-right: 40px;
				@include sm {
					padding-right: 24px; } }
			&:last-child {
				@include sm {
					padding-right: 16px; } } }
		&-list {
			position: relative;
			padding: 32px 32px 0;
			@include md {
				padding: 24px 16px 0; }
			@include sm {
				padding: 16px 16px 0; } } } }


.card-description {
	padding: 32px 32px 16px;
	@include md {
		padding: 24px 16px 8px; }
	&__title {
		margin-bottom: 16px; }
	&__subtitle {
		margin-bottom: 8px; }
	&__article {
		margin-bottom: 24px;
		@include md {
			margin-bottom: 16px; } } }

.card-characteristics {
	padding: 32px 32px 8px;
	@include clr;
	@include md {
		padding: 24px 16px 0; }
	&__title {
		margin-bottom: 16px; }
	&__box {
		margin-bottom: 32px;
		@include md {
			margin-bottom: 24px; } }
	&__list {
		border-top: 1px solid rgba($dark, 0.1); }
	&__item {
		padding: 8px 0;
		border-bottom: 1px solid rgba($dark, 0.1);
		@include clr; }
	&__label {
		float: left;
		width: 54%;
		color: $text; }
	&__value {
		float: right;
		width: 46%;
		color: $dark; }
	&__label,
	&__value {
		padding-right: 16px;
		@include sreg;
		@include f(14, 24);
		@include md {
			width: 50%;
			@include f(12, 24); }
		@include sm {
			padding: 4px 0;
			width: 100%;
			@include f(12, 16); } } }

.card-price {
	padding: 32px 15px 40px;
	@include md {
		padding: 24px 15px; }
	&__header {
		margin-bottom: 16px; }
	&__item {
		margin-bottom: 16px;
		&:last-child {
			@include sm {
				border-bottom: 1px solid rgba(28, 46, 73, 0.1); } } } }

.card-reviews {
	&__header {
		padding: 16px 32px 24px;
		@include md {
			padding: 24px 16px 16px; } }

	&__sorting {
		padding: 0 32px;
		@include clr;
		@include md {
			padding: 0 16px; } }

	&__comments {
		margin: 0 32px;
		border-top: 1px solid rgba($dark, 0.1);
		@include md {
			margin: 0 16px; } }

	&__nav {
		padding: 0 32px;
		@include clr;
		@include md {
			padding: 0 16px; } }

	&__form {
		padding: 0 32px 40px;
		@include md {
			padding: 0 16px 24px; }
		@include sm {
			padding: 8px 16px 28px; } } }

.card-overview {
	padding: 32px 32px 16px;
	@include md {
		padding: 24px 16px 0; }
	@include sm {
		padding: 24px 16px 8px; }
	&__item {
		margin-bottom: 32px;
		max-width: 760px;
		@include clr;
		@include md {
			margin-bottom: 24px; } }
	&__img {
		float: left;
		overflow: hidden;
		border-radius: 2px;
		width: 67%;
		height: 296px;
		font-size: 0;
		@include md {
			width: 68.5%;
			height: 272px; }
		@include sm {
			width: 100%;
			height: auto; }
		img {
			border-radius: 2px;
			width: 100%;
			height: 100%;
			object-fit: cover;
			@include sm {
				height: auto; } } }
	&__info {
		float: right;
		padding: 16px 0 0 32px;
		width: 33%;
		@include md {
			padding-left: 16px;
			width: 31.5%; }
		@include sm {
			width: 100%;
			padding: 16px 28px 0; } }
	&__sticker {
		margin-bottom: 8px; }
	&__subtitle {
		margin-bottom: 8px;
		color: $dark;
		@include reg;
		@include f(15, 24, 1); }
	&__description {
		margin-bottom: 32px;
		color: $text;
		@include sreg;
		@include f(13, 16);
		@include md {
			margin-bottom: 24px; } }
	&__meta {
		padding-bottom: 15px;
		border-bottom: 1px solid rgba($dark, 0.1);
		font-size: 0;
		li {
			display: inline-block;
			vertical-align: top;
			padding-right: 16px; } }
	&__more {
		padding: 16px 0 0;
		text-align: center;
		.btn {
			@include md {
				width: 100%; } } }
	&__product {
		margin-bottom: 24px;
		@include sm {
			margin-bottom: 16px; }
		&-in {
			display: block;
			@include hover {
				.card-overview {
					&__title,
					&__link {
						color: $blue; } } } } }
	&__title {
		@include t(color);
		@include sm {
			padding-bottom: 8px;
			@include f(14, 24, 0); } }
	&__link {
		color: $text;
		@include sreg;
		@include f(14, 24);
		@include t(color);
		@include md {
			@include f(12, 24); }
		@include sm {
			@include f(12, 16); } } }
