.subscribe {
	background: #e4e7ec;
	padding: 16px 0;
	@include md {
		padding: 24px 0; }
	&__inner {
		@include clr; }
	.icon-subscribe {
		position: absolute;
		top: 3px;
		left: 0;

		color: $blue-light;
		fill: $dark;

		font-size: 34px;
		@include md {
			top: -1px; } }
	&__title {
		position: relative;
		float: left;
		overflow: hidden;
		padding: 1px 0 7px 42px;
		color: $dark;
		@include reg;
		@include f(26, 32, 0.47);
		@include md {
			padding: 0 0 0 42px;
			@include f(24, 32, 0.44); }
		@include sm {
			margin-bottom: 16px;
			@include f(18, 32, 0.33); }
		span {
			display: block;
			line-height: 32px; } }
	&__label {
		float: left;
		padding: 5px 0 3px 16px;
		@include sreg;
		@include f(13, 16);
		@include md {
			display: none; }
		strong, span {
			display: block; }
		strong {
			color: $dark; }
		span {
			color: $text; } }
	&__form {
		float: right;
		width: 515px;
		@include md {
			float: none;
			overflow: hidden;
			padding-left: 40px;
			width: auto; }
		@include sm {
			clear: both;
			padding: 0; } }
	&__field {
		overflow: hidden;
		padding-right: 16px;
		// width: 370px
		@include md {
			width: auto; } }
	&__btn {
		float: right;
		@include sm {
			display: none; } }
	&__next {
		display: none;
		float: right;
		@include sm {
			display: block; } } }
