.recommended {
	padding: 8px 0 16px;
	&__row {
		margin: 0 -8px;
		@include clr;
		@include md {
			margin: 0 -6px; } }
	&__col {
		float: left;
		padding: 8px;
		@include md {
			padding: 8px 6px; }
		&_xs {
			width: 25%;
			&:last-child {
				@include md {
					display: none; } }
			&:nth-child(3) {
				@include sm {
					display: none; } }
			@include md {
				width: 33.33%; }
			@include sm {
				width: 50%; } }
		&_sm {
			width: 33.33%;
			&:nth-child(3),
			&:nth-child(4) {
				@include sm {
					display: none; } }
			@include sm {
				width: 50%; } } } }
