.score-schedule {
	padding: 8px 0 16px;
	@include md {
		padding-bottom: 8px; }
	@include sm {
		padding: 0 0 8px;
		border-top: 1px solid rgba($dark, 0.1); }
	&__header,
	&__row {
		@include clr;
		border-bottom: 1px solid rgba($dark, 0.1); }
	&__header {
		@include sm {
			display: none; } }
	&__row {
		padding: 4px 0;
		@include sm {
			padding: 12px 0; } }
	&__title-address,
	&__title-time,
	&__address div,
	&__time div {
		@include sreg;
		@include f(14, 24);
		@include md {
			@include f(12, 24); }
		@include sm {
			@include f(12, 16); } }
	&__title-address,
	&__title-time {
		padding-bottom: 8px;
		color: $darkGray; }
	&__title-address,
	&__address {
		float: left;
		width: 70%; }
	&__title-time,
	&__time {
		float: left;
		width: 30%; }
	&__address,
	&__time {
		color: $text;
		line-height: 48px;
		@include sm {
			width: 100%;
			line-height: normal; }
		div {
			display: inline-block;
			vertical-align: middle;
			@include sm {
				display: block; } } }
	&__address {
		@include sm {
			margin-bottom: 8px; } }
	&__time {
		span {
			display: block;
			@include sm {
				display: inline-block; } } } }
