.nav-list {
	position: relative;
	margin-bottom: 40px;
	font-size: 0;
	@include md {
		overflow: auto;
		padding: 0 16px;
		margin: 0 -16px;
		white-space: nowrap; }
	&__inner {
		@include md {
			display: inline-block;
			vertical-align: top;
			border-bottom: 1px solid rgba(29, 46, 73, 0.1);
			min-width: 100%; }
		@include sm {
			border-bottom: none; } }
	&__item {
		line-height: 1;
		@include md {
			display: inline-block;
			vertical-align: top; }
		&:not(.nav-list__item_last) {
			margin-bottom: 16px;
			@include md {
				margin: 0 40px 0 0; }
			@include sm {
				margin-right: 24px; } }
		&:last-child {
			&:not(.nav-list__item_last) {
				margin-bottom: 0;
				@include md {
					margin-right: 0; }
				@include sm {
					margin-right: 0; } } }
		&_last {
			padding-top: 16px;
			border-top: 1px solid rgba(29, 46, 73, 0.1);
			@include md {
				padding-top: 0;
				border: none; } } }
	&__link {
		display: block;
		color: $dark;
		@include reg;
		@include f(15, 24, 0.27);
		@include t(color);
		@include hover {
			color: $blue; }
		&.is-active {
			color: $blue;
			&:before {
				@include md {
					transform: translate3d(0, 0, 0) scale(1); } } }
		@include md {
			position: relative;
			padding-bottom: 16px; }
		@include sm {
			padding-bottom: 8px;
			@include f(14, 24, 0.25); }
		&:before {
			content: '';
			position: absolute;
			z-index: 1;
			bottom: 0;
			left: 0;
			right: 0;
			display: none;
			border-radius: 2px;
			height: 2px;
			background-color: $blue;
			transform: translate3d(0, 0, 0) scale(0);
			@include tr(transform 0.2s);
			@include md {
				display: block; }
			@include sm {
				// right: -5px
 }				// left: -5px
			.ps-active-x & {
				display: none; } } } }
