.footer {
	font-size: 0;
	padding: 24px 0 16px;
	@include md {
		padding: 8px 0 16px; }
	&__row {
		@include md {
			padding-bottom: 16px; } }
	&__col {
		display: inline-block;
		vertical-align: top;
		width: 25%;
		@include md {
			display: block;
			width: 100%; }
		&:last-child {
			.footer__nav {
				@include md {
					border: none; } } } }
	&__nav {
		padding-bottom: 24px;
		@include md {
			padding-bottom: 0;
			border-bottom: 1px solid rgba($dark, 0.3); } }
	&__title {
		position: relative;
		padding: 8px 0;
		color: $dark;
		@include reg;
		@include f(15, 24, 0.27);
		@include md {
			cursor: pointer;
			user-select: none; }
		&.is-active {
			.icon {
				transform: translate3d(0,0,0) rotate(180deg); } }
		.icon {
			position: absolute;
			top: 16px;
			right: 7px;
			display: none;
			fill: $dark;
			font-size: 6px;
			@include gpu;
			@include t(transform);
			@include md {
				display: block; } } }
	&__list {
		@include md {
			display: none;
			padding-bottom: 16px; }
		a {
			color: $text;
			@include sreg;
			@include f(13, 24, null);
			@include t(color);
			@include hover {
				color: $blue; } } }
	&__bottom {
		padding: 12px 0 0;
		border-top: 1px solid rgba($dark, 0.1);
		@include clr;
		@include md {
			padding: 16px 0 0;
			border-top-color: rgba($dark, 0.3); } }
	&__logo {
		float: left;
		overflow: hidden;
		margin-right: 26px; }
	&__copy {
		float: left;
		padding: 2px 0 1px;
		color: $text;
		@include sreg;
		@include f(13, 24, null);
		@include sm {
			float: right; } }
	&__social {
		float: right;
		padding-top: 3px;
		width: 25%;
		@include md {
			width: 100%;
			max-width: 300px;
			text-align: right; }
		@include sm {
			float: none;
			padding: 0 0 22px;
			max-width: 100%;
			text-align: center; }
		li,
		a {
			display: inline-block;
			vertical-align: top; }
		li {
			padding-right: 16px;
			&:last-child {
				padding-right: 0; } }
		a {
			overflow: hidden;
			width: 24px;
			height: 24px;
			@include vertical;
			white-space: nowrap;
			@include hover {
				.icon {
					fill: $blue; } }
			@include active {
				.icon {
					fill: $blue-dark; } } }
		.icon {
			display: inline-block;
			vertical-align: middle; }
		.icon-vk {
			font-size: 12px; }
		.icon-fb {
			font-size: 16px; }
		.icon-od {
			font-size: 17px; }
		.icon-youtube {
			font-size: 14px; }
		.icon-insta {
			font-size: 20px; }
		.icon-google {
			font-size: 15px; } } }
