.card-item {
	@include clr;
	@include md {
		padding-bottom: 28px; }
	@include sm {
		padding-bottom: 24px; }
	&__title {
		padding-bottom: 8px;
		color: $dark;
		@include reg;
		@include f(22,32,1);
		@include md {
			float: left;
			padding-bottom: 0;
			width: 54.6%;
			@include f(20,24,1); }
		@include sm {
			float: none;
			padding-bottom: 8px;
			width: auto;
			@include f(16,24,1); } }
	&__body {
		@include md {
			float: left;
			padding-left: 32px;
			width: 45.4%; }
		@include sm {
			float: none;
			width: auto;
			padding-left: 0; } }
	&__price {
		padding-bottom: 8px;
		font-size: 0;
		@include md {
			padding-bottom: 0; }
		@include sm {
			padding-bottom: 8px; }
		&-current,
		&-old {
			display: inline-block;
			vertical-align: top; }
		&-current {
			padding-right: 16px;
			color: $blue;
			@include bold;
			@include f(32,40,1);
			@include md {
				padding-right: 8px;
				@include f(20,24,1); }
			@include sm {
				@include f(16,24,1); } }
		&-old {
			color: $text;
			@include med;
			@include f(15,40,1);
			text-decoration: line-through;
			@include md {
				@include f(13,24,1); }
			@include sm {
				@include f(12,24,1); } } }
	&__info {
		@include md {
			float: left;
			padding-right: 24px; } }
	&__field {
		padding-bottom: 8px;
		font-size: 0;
		@include md {
			padding-bottom: 0; } }
	&__label,
	&__value {
		display: inline-block;
		vertical-align: top;
		@include sreg;
		@include f(13,24);
		@include md {
			@include f(12,24); } }
	&__label {
		padding-right: 8px;
		color: $text; }
	&__value {
		color: $dark; }
	&__rating {
		padding-bottom: 31px;
		@include md {
			float: right;
			padding-bottom: 0; }
		@include sm {
			float: left; } } }
